.access-bar {
  background: #223b5d;
  color: #fff;
}

.access-bar a {
  font-size: .875em;
  color: #fff;
  display: flex;
  align-items: center;
  text-decoration: none;
  font-weight: 500;
  font-family: DIN-Alternate, Helvetica, Arial, sans-serif;
}

.access-bar a svg {
  width: .875rem;
  height: .875rem;
  margin-right: .9375rem;
}

.access-bar a:hover,
.access-bar a:focus {
  color: #dba531;
  text-decoration: none;
}

.access-bar_inner {
  height: 1.875em;
}

.access-bar_link {
  display: flex;
}

.access-bar_link-phone,
.access-bar_link-callback,
.access-bar_link-first-visit,
.access-bar_link-account,
.access-bar_link-cart {
  display: flex;
  align-items: stretch;
}

.access-bar_link-phone a,
.access-bar_link-callback a,
.access-bar_link-first-visit a,
.access-bar_link-account a,
.access-bar_link-cart a {
  padding-right: 1.875em;
}

.access-bar_link-phone svg,
.access-bar_link-callback svg,
.access-bar_link-first-visit svg {
  color: #dba531;
}

.access-bar_link-phone,
.access-bar_link-callback,
.access-bar_link-account {
  border-right: 1px solid rgba(124,129,139,0.4);
}

.access-bar_link-callback a,
.access-bar_link-first-visit a,
.access-bar_link-cart a {
  padding-left: 1.875em;
}

.access-bar_link-first-visit {
  flex-grow: 1;
}

.access-bar_link-cart .number {
  display: inline-block;
  color: #fff;
  position: relative;
  font-weight: 700;
  margin-left: .625rem;
  font-family: DIN-Alternate, Helvetica, Arial, sans-serif;
}

.access-bar_link-cart .number span {
  position: relative;
  z-index: 2;
}

.access-bar_link-cart .number::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 1.1875rem;
  height: 1.1875rem;
  background: #dba531;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
